.FilterItem {
    border-bottom: solid gray 1px;
    margin-bottom: 1em;
    padding-bottom: 1em;
    display: flex;
    justify-content: right;
    align-items: center;
}

.FilterItem > span {
    width: 50%;
    margin-right: auto;
    margin-left: 0;
    text-align: left;
}

.FilterItem.btn{
    padding: 14px 28px;
}

.SpaceMarineSearch {
    padding: 1%;
    width: 100%;
    margin-left: 0;
    margin-right: auto;
    border: solid blue 1px;
    border-radius: 12px;
    height: 90%;
    overflow-y: auto;
}


.searchWrapper {
    height: 85vh;
}