.SpaceShipCard {
    height: 10vh;
    border: solid 1px black;
    border-radius: 12px;
    display: block;
    padding: 1%;
}

.SpaceShipCard > Button {
    float: right;
    height: 100%;
}

.SpaceShipCard.MuiInputBase-input {
    display: flex;
    float: right;
}

.SpaceShipId {
    font-size: 4em;
    float: left;
}

.SpaceShipMarineList {
    margin-left: 3%;
    margin-right: 3%;
    overflow-y: auto;
    height: 100%;
    width: 10%;
    float: left;
}

.SpaceShipMarineListWrapper {
    float: left;
    margin-left: 3%;
    align-items: center;
    display: flex;
    height: 100%;
}